<template>
    <div>
        <section class="modals">
            <div class="container products">
                <div class="modal fade" id="unread-messages" role="dialog" tabindex="-1" aria-hidden="true">
                    <div class="modal-dialog modal-lg">
                        <div class="modal-content unread-messages" ref="load_licence">
                            <button v-if="products.status === true " type="button" style="position: absolute;top: 20px; left: 20px;z-index: 2;">
                                <button @click="setBackButton" class="btn btn-dark btn-sm">Back</button>
                            </button>
                            <button type="button" style="position: absolute;top: 20px; right: 20px;z-index: 2;font-size: 34px;" class="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                            <div class="modal-body">
                                <div class="chatting-page" style="min-height: 400px;">
                                    <div class="container">
                                        <div class="row">
                                            <div class="space"></div>
                                            <div class="col-lg-12">
                                                <h3 style="margin-left: 50px">{{ selectText }}</h3>
                                                <div class="space">

                                                </div>
                                            </div>
                                            <spinner :data="spinnerData"></spinner>
                                            <span style="width: 100%" v-if="categories.status === true">
                                                <div class="row">
                                                    <div class="col col-lg-3 col-md-6 col-sm-12" v-for="(category, index) in categories.list " :key="index">
                                                        <div class="category">
                                                            <div class="category__image">
                                                                <img src="img/category/cat1.png" alt="">
                                                                <span class="category__icon" :style=" 'font-size: 30px;;color:' + category.icon_color " ><i class="fas fa-atom"></i></span>
                                                            </div>
                                                            <div class="category__info">
                                                                <p>{{ category.category_description }}</p>
                                                                <h5 style="font-weight: bold">{{ category.category_name }}</h5>
                                                                <br>
                                                                <button @click="getProductsByCategory(category.category_id)" class="apply-btn">Select</button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </span>
                                            <span v-else class="w-100 h-100 d-flex align-content-center justify-content-center" style="color:#6c757d">
                                                <h1>{{categories.message}}</h1>
                                            </span>

                                            <span style="width:100%" v-if="products.status === true ">
                                                <div class="row">
                                                    <div v-for="(product, index) in products.list" :key="index" class="col col-lg-12 col-md-12 col-sm-12">
                                                        <article class="feature-full__job-posts">
                                                            <div class="row">
                                                                <div class="company-logo">
                                                                    <a href="#"><img style="width: 80px;height: 80px" :src="product.product_image" alt=""></a>
                                                                </div>
                                                                <div class="job-description">
                                                                    <h5 class="job-title"><a href="#">{{ product.product_description }} </a></h5>
                                                                    <span class="location"><a href="#"><i class="fas fa-map-marker-alt"></i> {{ product.product_name }}</a></span>
                                                                    <span class="job-category cat-color-one"><a href="#">{{ product.companies ? product.companies[0].company_name : '' }}</a></span>
                                                                </div>
                                                                <div class="budget">
                                                                    <ul>
                                                                        <li class="amount"><p>₦{{ product.product_value }}</p></li>
                                                                    </ul>
                                                                </div>
                                                                <div class="apply">
                                                                    <button @click="selectProduct(product.product_id)" class="apply-btn">Choose</button>
                                                                </div>
                                                            </div>
                                                        </article>
                                                    </div>
                                                </div>
                                            </span>
                                            <span v-else class="w-100 h-100 d-flex align-content-center justify-content-center" style="color:#6c757d">
                                                <h1>{{products.message}}</h1>
                                            </span>

                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
    import {mapState} from 'vuex';
    export default {
        name: "ProductsModal",
        data() {
          return {
              products: {
                  list: {},
                  status: false,
                  message: '',
              },
              categories: {
                  list: {},
                  status: false,
                  message: "",
              },
              BackCategories: {
                  list: {},
                  status: false,
                  message: "",
              },
              selectText: "",
              spinnerData: {},
          }
        },
        computed: {
            ...mapState(['tempStore']),
            getTempStore() {
                return this.tempStore
            },
        },
        mounted() {
            window.eventBus.$on("industryId", id => {
                this.getCategoriesByIndustry(id);
            });
            $('#unread-messages').on('hidden.bs.modal', function () {
                alert('modal closed');
            })
        },
        methods: {
            // show the previously selected category
            setBackButton() {
                this.products = {
                    list: {},
                    status: false,
                    message: '',
                };
                this.categories = {
                    list: this.Backcategories.list,
                    status: this.Backcategories.status,
                    message: this.Backcategories.message,
                };
                this.selectText = 'Select Industry Category'
            },
            showLoading(options = {}) {
                let loader = this.spinnerData = {
                    spinner: 'Google',
                    isLoading: options.show !== undefined ? options.show : true ,
                    color: '#00bfff',
                    size: '60px',
                    options: 'center absolute',
                };
                return loader;
            },
          getProductsByCategory(id) {
            this.$store.commit("SET_TEMP_STORE",id);
              this.products = {
                  list: {},
                  status: false,
                  message: '',
              };
              this.showLoading();
              this.axios
                  .get(`category/products/${id}`)
                  .then(response => {
                      this.showLoading({show:false});
                      if (response.data.data.status === false) {
                          this.products = {
                              list: {},
                              status: false,
                              message: 'Sorry no product could be found',
                          };
                          this.categories = {}
                      }else {
                          // console.log(response.data.data.products);
                          this.selectText = "Choose Product";
                          this.categories = {};
                          this.products = {
                              list: response.data.data.products,
                              status: true,
                              message: '',
                          };
                      }
                  })
                  .catch(error => {
                      // console.log(error.response);
                      this.products = {
                          list: {},
                          status: false,
                          message: '',
                      };
                      this.showLoading();
                  });
          },
            selectProduct(id) {
                $(".close").click();
                let val = this.$store.state.tempStore;
                let dt = id + '/' + val;
                let enc = this.$encrypto('E',dt);
                this.$store.commit("SET_TEMP_STORE",true);
                if (enc) {
                    // console.log(enc)
                    // this.$router.push('/licence/' + enc );
                    this.$router.push({ name: 'Licence', params: { id: enc } })
                }
            },
            getCategoriesByIndustry(id) {
                this.categories = {
                    list: {},
                    status: false,
                    message: '',
                };
                this.products = {
                    list: {},
                    status: false,
                    message: '',
                };
                this.showLoading();
                this.axios
                    .get(`industries/categories/${id}`)
                    .then(response => {
                        this.showLoading({show:false});
                        if (response.status === 200) {
                            if (response.data.data.status === false) {
                                this.categories = {
                                    list: {},
                                    status: false,
                                    message: 'Sorry no category could be found',
                                };
                            }else {
                                // console.log('categories',response.data.data.categories);
                                this.products = {};
                                this.selectText = "Select Industry Category";
                                let categories = response.data.data.categories;
                                let categoryData = [];

                                for (var key in categories) {
                                    var value = categories[key];
                                    var formFields = {
                                        category_description: value.category_description,
                                        category_id: value.category_id,
                                        category_name: value.category_name,
                                        icon_color: this.getRandomColor(),
                                    };
                                    categoryData.push(formFields);
                                };
                                this.categories = {
                                    list: categoryData,
                                    status: true,
                                    message: '',
                                };
                                this.Backcategories = {
                                    list: categoryData,
                                    status: true,
                                    message: '',
                                };
                            }
                        }
                    })
                    .catch(error => {
                        // console.log(error);
                        this.categories = {
                            list: {},
                            status: false,
                            message: error.response ? error.response.data.message : "Sorry an error occurred, check your internet",
                        };
                        this.showLoading();
                    });
            },
            getRandomColor() {
                var letters = '0123456789ABCDEF';
                var color = '#';
                for (var i = 0; i < 6; i++) {
                    color += letters[Math.floor(Math.random() * 16)];
                }
                return color;
            },
        },
    }
</script>

<style scoped>
.products .modal-lg {
    max-width: 80% !important;
}
    .products .company-logo {
        margin-bottom: 30px;
    }
    .products .apply {
        margin-top: 30px;
        float: left;
    }
</style>
